
import { ref, computed } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { currencies } from "@/views/currenciesList";

const menuList = [
  {
    icon: 'attach_money',
    label: 'Счета',
    name: 'accounts',
    separator: true
  },
  {
    icon: 'history',
    label: 'История',
    name: 'history',
    separator: true
  },
  {
    icon: 'category',
    label: 'Категории',
    name: 'categories',
    separator: true
  },
  {
    icon: 'next_plan',
    label: 'Цели',
    name: 'goals',
    separator: true
  },
  {
    icon: 'account_balance',
    label: 'Обязательства',
    name: 'obligations',
    separator: true
  },
  {
    icon: 'add',
    label: 'Новая запись',
    name: 'add-note',
    separator: true
  },
]

export default {
  name: "MainLayout",
  setup () {
    const activeItem = ref('Счета');
    const router = useRouter();
    const currentRoute = useRoute();

    function onClickMenuItem (item: {label: string, name: string}) {
      activeItem.value = item.label;
      router.push({name: item.name});
    }

    const currentCurrency = ref({id: 1, name: 'Доллар', icon: '$',});
    const getCurrentCurrency = computed(() => currentCurrency.value.icon)
    function setCurrentCurrency (currency: {id: number, name: string, icon: string}) {
      currentCurrency.value = currency;
    }

    return {
      drawer: ref(false),
      activeItem,
      menuList,
      currencies,
      onClickMenuItem,
      currentCurrency,
      getCurrentCurrency,
      setCurrentCurrency,
      currentRoute,
    }
  }
}
